import React, { useEffect, useState } from "react";
import styles from "../styles/SafeSpace.module.css";
import Strip from "../components/Strip";
import firebase from "../helpers/firebase";
import Photo from "../components/Photo";

const SafeSpace = (props) => {
  const [data, setData] = useState(null);
  const db = firebase.firestore();
  useEffect(() => {
    const rdata = JSON.parse(sessionStorage.getItem("data"));
    db.collection("safespace")
      .doc(rdata.name)
      .get()
      .then((doc) => {
        setData(doc.data());
        console.log(doc.data());
        sessionStorage.setItem(
          "data",
          JSON.stringify({ name: rdata.name, ...doc.data() })
        );
      });
  }, []);
  return (
    <div className={styles.wrapper}>
      {data ? (
        <>
          <h1 className={styles.title}>SafeSpace {data.name}</h1>
          <Strip title="Founder" />
          {data.members
            .filter((ele) => ele.role === "Founder")
            .map((mem) => (
              <div className={styles.card}>
                <Photo title={mem.name} url={mem.url} />
              </div>
            ))}
          <Strip title="Member" />
          {data.members
            .filter((ele) => ele.role === "Member")
            .map((mem) => (
              <div className={styles.card}>
                <Photo title={mem.name} url={mem.url} />
              </div>
            ))}
        </>
      ) : null}
    </div>
  );
};

export default SafeSpace;
